@import "scss/common.scss";

.button {
  @include borders;
  @include buttons;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;

  &.visible {
    border-color: $borderDark;
    border-width: 1px;
  }

  &.red {
    background-color: $red;
  }

  span {
    font-size: 12px;
  }

  &.value-1 {
    color: $blue;
  }
  &.value-2 {
    color: $green;
  }
  &.value-3 {
    color: $red;
  }
  &.value-4 {
    color: $purple;
  }
  &.value-5 {
    color: $maroon;
  }
  &.value-6 {
    color: $turquoise;
  }
  &.value-7 {
    color: $black;
  }
  &.value-8 {
    color: $gray;
  }
}
